.chevron-bottom:before {
  border-style: solid;
  border-width: 2px 2px 0 0;
  content: '';
  display: inline-block;
  height: 16px;
  width: 16px;
  top: 7px;
  left: 0;
  position: relative;
  transform: rotate(-45deg);
  vertical-align: top;
}

.chevron-right:before {
  border-style: solid;
  border-width: 1px 1px 0 0;
  border-color: #000;
  content: '';
  display: inline-block;
  height: 16px;
  width: 16px;
  top: 7px;
  left: 0;
  position: relative;
  transform: rotate(45deg);
  vertical-align: top;
}

.chevron-left:before {
  border-style: solid;
  border-width: 1px 1px 0 0;
  border-color: #000;
  content: '';
  display: inline-block;
  height: 16px;
  width: 16px;
  top: 7px;
  right: -6px;
  position: relative;
  transform: rotate(-130deg);
  vertical-align: top;
}

.go-top {
  border-radius: 5px;
  background-color: #010e1b;
  background-color: rgba(1, 14, 27, 0.7);
  position: fixed;
  width: 45px;
  height: 45px;
  display: block;
  right: 15px;
  bottom: 15px;
  border: none;
  opacity: 0;
  z-index: -1;
  transition: all .4s ease-in;
}

.go-top:hover {
  opacity: 1;
  background-color: #010e1b;
  background-color: rgba(1, 14, 27, 0.9);
}

.go-top.is-active {
  opacity: .8;
  z-index: 4;
}

.embed-responsive {
  position: relative;
  display: block;
  height: 0;
  padding: 0;
  overflow: hidden;
}

.embed-responsive .embed-responsive-item,
.embed-responsive iframe,
.embed-responsive embed,
.embed-responsive object,
.embed-responsive video {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}

.embed-responsive-16by9 {
